.mainhead
  gap: .5rem

  .searchbar
    display: flex
    align-items: center


    input
      height: 2.5rem
      padding: .25em .75em
      width: clamp(100px, 40vw, 300px )
      font-weight: bold
      font-size: 1.125rem
      border: 1px solid grey
      outline: 1px solid purple
      

    span
      padding: .7rem
      background-color: purple

  .usericon
    display: flex
    align-items: center
    gap: 1rem

  @media ( max-width: 600px )
    .searchbar
      input
        // height: 2rem
        // padding: .2rem .7rem
        

      span
        // padding: .43rem

    .usericon
      display: flex
      align-items: center
      gap: .5rem



  
.mainnav
  a
    text-decoration: none
    color: #ccc
  li
    display: flex
    align-items: center
    gap: 1rem

    &:hover
      color: #222

  @media ( max-width: 450px )
      span
        display: none


    