.popcard
  background: #eee
  border-radius: 1rem
  padding: 1rem
  color: purple
  position: fixed
  margin: 2rem
  line-height: 1.5
  display: flex
  flex-direction: column
  display: none
 
  header
    text-align: center
    color: darken(purple, 8% )
    font-size: 2rem
    font-weight: 900
  p
    line-height: 2.5
  button
    padding: .5em 1.125em
    margin: 1rem
    outline: none
    border: none
    width: fit-content
    align-self: end
    font-weight: 900
    background: lighten(purple, 30% )
    color: darken(purple, 20% )

