.mainhead {
  gap: 0.5rem;
}
.mainhead .searchbar {
  display: flex;
  align-items: center;
}
.mainhead .searchbar input {
  height: 2.5rem;
  padding: 0.25em 0.75em;
  width: clamp(100px, 40vw, 300px);
  font-weight: bold;
  font-size: 1.125rem;
  border: 1px solid grey;
  outline: 1px solid purple;
}
.mainhead .searchbar span {
  padding: 0.7rem;
  background-color: purple;
}
.mainhead .usericon {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 600px) {
  .mainhead .usericon {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
}

.mainnav a {
  text-decoration: none;
  color: #ccc;
}
.mainnav li {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.mainnav li:hover {
  color: #222;
}
@media (max-width: 450px) {
  .mainnav span {
    display: none;
  }
}

.popcard {
  background: #eee;
  border-radius: 1rem;
  padding: 1rem;
  color: purple;
  position: fixed;
  margin: 2rem;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  display: none;
}
.popcard header {
  text-align: center;
  color: #570057;
  font-size: 2rem;
  font-weight: 900;
}
.popcard p {
  line-height: 2.5;
}
.popcard button {
  padding: 0.5em 1.125em;
  margin: 1rem;
  outline: none;
  border: none;
  width: fit-content;
  align-self: end;
  font-weight: 900;
  background: #ff1aff;
  color: #1a001a;
}

